.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.black-nav {
  width: 100vw;
  height: 60px;
  background-color: black;
  display: flex;
  flex-direction: row;
  color: white;
  padding-left: 20px;
}

.list {
  width: 100vw;
  height: 100px;
  padding-top: 10px;
  padding-left: 30px;
  text-align: left;
  border-bottom: 1px solid gray;
}
.list h3 {
  width: 30%;
  display: inline-block;
}
.list h5 {
  width: 10%;
  display: inline-block;
}
.list p {
  margin-top: -10px;
}
.list button {
  width: 30px;
  font-size: 20px;
  float: right;
  margin-top: -80px;
  border: none;
  background: transparent;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  text-align: left;
  background: #eee;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}/*# sourceMappingURL=App.css.map */